os.")])])])]),_c('div',{staticClass:"tarjeta color-custom p-4",attrs:{"titulo":"Administración"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-xl-2 col-lg-3 col-4 mb-lg-0 mb-4"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"124px"},attrs:{"src":require("@/assets/curso/tema4/avt5.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"h4"},[_vm._v("Responsable de información y administración")]),_c('p',[_vm._v("Lleva a cabo los procesos administrativos del almacén y da respuesta a las necesidades de información.")])])])]),_c('div',{staticClass:"tarjeta color-custom p-4",attrs:{"titulo":"Auxiliar"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-xl-2 col-lg-3 col-4 mb-lg-0 mb-4"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"124px"},attrs:{"src":require("@/assets/curso/tema4/avt6.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"h4"},[_vm._v("Auxiliar de almacén / repartidor / reponedor / carretillero")]),_c('p',[_vm._v("Estos puestos se encargan de las mercancías una vez recepcionadas y antes de ser expedidas: las transportan y ubican, las distribuyen, las recuentan, las clasifican, etc.")])])])])])],1),_c('separador'),_vm._m(2),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("En un almacén las manipulaciones de bienes almacenados son muchos, y por eso aumentan los riesgos, de daños, costos en el tiempo y mano de obra, por eso se hacen importantes los equipos de manejo entre otros tenemos:")]),_c('SlyderB',{staticClass:"mb-5",attrs:{"datos":_vm.datosSlyder,"data-aos":"zoom-in"}}),_c('separador'),_vm._m(3),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Las TIC se han convertido en un medio para agilizar, flexibilizar y mejorar el intercambio de información y operaciones utilizadas en la gest